@media screen and (max-width: 960px) {
  .icon {
    position: absolute;
    right: 20px;
    top: 25px;
    color: black;
    transition: 0.5s;
    font-size: 1.5rem;
  }

  .icon-rotate {
    position: absolute;
    right: 20px;
    top: 20px;
    color: black;
    transition: 0.5s;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-size: 1.5rem;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 83vh;
    position: absolute;
    top: 85px;
    right: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
  }

  .nav-menu.active {
    background: rgb(226, 224, 224);
    right: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  nav .link {
    font-size: 1.5rem;
  }

  nav li {
    padding-top: 100px;
  }

  .logo {
    font-size: 1.5rem;
  }
}

